.default {
    /* fill: none; */
    stroke: white;
    stroke-linejoin: round;
    stroke-linecap: round;
    stroke-opacity: 1;
    stroke-width: 3px;

    stroke-dasharray: 1000;
    stroke-dashoffset: -600;
    -webkit-animation: dash 3s linear forwards;
            animation: dash 3s linear forwards;
}

@-webkit-keyframes dash {
    to {
      stroke-dashoffset: 0;
    }
  }

@keyframes dash {
    to {
      stroke-dashoffset: 0;
    }
  }

.forhover {
    fill: transparent;
    /* fill: rebeccapurple; */
    opacity: 0.5;
}

.forhover:hover {
    cursor: pointer;
    stroke: rgb(255, 255, 255);
    stroke-linejoin: round;
    stroke-opacity: 0.1;
    stroke-width: 5px;
    /* fill: blue; */
}

.leftarrow {
    /* background: pink; */
    position: absolute;
    left: -55px;
    bottom: 150px;
    color: rgb(180, 180, 180);
    opacity: .5;

    -webkit-animation-name: bounceAlpha;

            animation-name: bounceAlpha;
    -webkit-animation-duration:1.4s;
            animation-duration:1.4s;
    -webkit-animation-delay:0.2s;
            animation-delay:0.2s;
    -webkit-animation-iteration-count:infinite;
            animation-iteration-count:infinite;
    -webkit-animation-timing-function:linear;
            animation-timing-function:linear;
}

@-webkit-keyframes bounceAlpha {
    0% {-webkit-transform: translateX(0px) scale(1);transform: translateX(0px) scale(1);}
    25%{-webkit-transform:translateX(10px) scale(1);transform:translateX(10px) scale(1);}
    26%{-webkit-transform:translateX(-10px) scale(1);transform:translateX(-10px) scale(1);}
    55% {-webkit-transform: translateX(0px) scale(1);transform: translateX(0px) scale(1);}

    /* 0% {opacity: 1; transform: translateX(0px) scale(1);}
  25%{opacity: 0; transform:translateX(10px) scale(0.9);}
  26%{opacity: 0; transform:translateX(-10px) scale(0.9);}
  55% {opacity: 1; transform: translateX(0px) scale(1);} */
}

@keyframes bounceAlpha {
    0% {-webkit-transform: translateX(0px) scale(1);transform: translateX(0px) scale(1);}
    25%{-webkit-transform:translateX(10px) scale(1);transform:translateX(10px) scale(1);}
    26%{-webkit-transform:translateX(-10px) scale(1);transform:translateX(-10px) scale(1);}
    55% {-webkit-transform: translateX(0px) scale(1);transform: translateX(0px) scale(1);}

    /* 0% {opacity: 1; transform: translateX(0px) scale(1);}
  25%{opacity: 0; transform:translateX(10px) scale(0.9);}
  26%{opacity: 0; transform:translateX(-10px) scale(0.9);}
  55% {opacity: 1; transform: translateX(0px) scale(1);} */
}

