    font-size: 16px;
    color: rgb(255, 255, 255);
    border-radius: 10px;

    -webkit-transition: width 1s;

    transition: width 1s;
}


.accomplish_box .box .accomplish {
    /* background: goldenrod; */
    position: absolute;
    left: 4px;
    width: 600px;
    height: 50px;
    
    -webkit-transition: left 1s;
    
    transition: left 1s;
}

.accomplish_box.open .box {
    width: 400px;
}

.accomplish_box.open .box .accomplish{
    left: -100px;
}

.accomplish_box.open .box:hover {
    cursor: pointer;
    -webkit-transition: width 0.5s;
    transition: width 0.5s;
    width: 450px;
}





